<template>
    <v-container>
                 <v-card>
                    <v-card-title>
                        <v-text-field v-model="display"
                                      readonly
                                      outlined
                                      dense
                                      class="text-right"
                                      variant="outlined"
                                      label="Display"
                                      hide-details />
                    </v-card-title>
                    <v-card-subtitle>
                        <v-row>
                            <v-col v-for="btn in buttons" :key="btn" cols="3">
                                <v-btn @click="appendToDisplay(btn )"
                                       block
                                       :color="btn.color">
                                    {{ btn.text }}
                                </v-btn>
                            </v-col>
                        </v-row>

                        <v-btn @click="clearDisplay"
                               block
                               color="red">
                            C
                        </v-btn>
                    </v-card-subtitle>
                </v-card>
            
    </v-container>
</template>

<script>
    import axios from '../plugins/axios'
    export default {
        name: 'CalculatorComponent ',
        data() {
            return {
                display: '',
                 input1: '',
                input2: '',
                operation: null,
                resultcalculated: false,
                result: null,
                buttons: [
                    { text: '7', color: 'primary', type: 'number' },
                    { text: '8', color: 'primary', type: 'number' },
                    { text: '9', color: 'primary', type: 'number' },
                    { text: '/', color: 'secondary', type: 'operation' },
                    { text: '4', color: 'primary', type: 'number' },
                    { text: '5', color: 'primary', type: 'number' },
                    { text: '6', color: 'primary', type: 'number' },
                    { text: '*', color: 'secondary', type: 'operation' },
                    { text: '1', color: 'primary', type: 'number' },
                    { text: '2', color: 'primary', type: 'number' },
                    { text: '3', color: 'primary', type: 'number' },
                    { text: '-', color: 'secondary', type: 'operation' },
                    { text: '0', color: 'primary', type: 'number' },
                    { text: '.', color: 'primary', type: 'number' },
                    { text: '=', color: 'secondary', type: 'result' },
                    { text: '+', color: 'secondary', type: 'operation' },
                ],
                headers: [

                    { title: 'Kalkulace', value: 'calculation' },
                    { title: 'Datum operace', value: 'createdOn' },

                ]
            };
        },
 
        methods: {
            addFunc() {
                this.result = parseFloat(this.input1) + parseFloat(this.input2);
            },
            divideFunc() {
                this.result = parseFloat(this.input1) / parseFloat(this.input2);
            },
            multipleFunc() {
                this.result = parseFloat(this.input1) * parseFloat(this.input2);
            },
            minusFunc() {
                this.result = parseFloat(this.input1) - parseFloat(this.input2);
            },

            appendToDisplay(value) {
                if (this.resultcalculated) {
                    this.clearDisplay();

                }


                if (value.type === 'result') {
                    this.resultcalculated = true;
                    this.calculateResult();
                } else if (value.type === 'operation') {
                    this.operation = value.text;
                    this.display += value.text.toString();
                }
                else {
                    if (this.operation != null && this.display.indexOf(this.operation) > 0) {
                        this.input2 += value.text;
                    } else {
                        this.input1 += value.text;
                    }
                    this.display += value.text.toString();
                }

            },
 
            sendError(obj) {
                let message = { message: obj };
                axios.post('/home/Error', message);
            },

            clearDisplay() {
                this.input1 = '';
                this.input2 = '';
                this.operation = null,
                    this.result = null,
                    this.display = '';
                this.resultcalculated = false;
            },
            calculateResult() {
                try {
                    switch (this.operation) {
                        case '*': this.multipleFunc(); break;
                        case '/': this.divideFunc(); break;
                        case '+': this.addFunc(); break;
                        case '-': this.minusFunc(); break;
                        default: break;
                    }
                    this.display = this.result;
                    this.resultcalculated = true;
                    let data = {
                        calculation: `${this.input1} ${this.operation} ${this.input2} = ${this.result}`,
                        input1: this.input1.toString(),
                        input2: this.input2.toString(),
                        operation: this.operation.toString(),
                        result: this.result.toString()
                    };
                    axios.post('/home', data).then(response => {
                        console.log('Data odesl�na na server:', response.data);
                        this.$emit('update-history'); // Emituje ud�lost
                        
                    }).catch(error => {
                        //console.error('Chyba p�i odes�l�n� po�adavku:', error);
                        this.sendError(error);

                    });

                } catch (e) {

                    this.display = 'Error';
                }
            }
        },
       
    };
</script>

<style scoped>
    .v-card {
        margin: 16px;
    }

    .v-text-field {
        font-size: 2em;
    }

    .v-btn {
        margin: 4px;
    }
</style>
